						</label>
					</tui-list-cell> -->
					<tui-list-cell unlined v-for="(item,index) in payway">
						<label class="tui-pay-item" @tap="paySelecter(item.way)">
							<image :src="item.icon" class="tui-pay-logo"></image>
							<text>{{item.show}}</text>
							<view class="tui-radio">
								<radio color="#EB0909" name="pay" :checked="true" v-if="index == 0"></radio>
								<radio color="#EB0909" name="pay" v-else></radio>
							</view>
						</label>
