</template>

<script>
	let app = getApp()
	export default {
		data() {
			return {
				hasCoupon: true,
				insufficient: false,
				show: false,
				showPay: false,
				hasAddress: false,
				// 渲染类数据
				address: {},
				orderList: [],
				totalPrice: 0,
				remark:'',
				payway:[
					{
						id:0,
						way:'vx',
						show:'微信支付',
						icon:'/static/images/mall/pay/icon_pay_weixin.png'
					},
				],
				paySelect:'vx',
				paybtn:false
			}
		},
		computed: {
			fixPhone() {
				return (phone) => {
					return phone.toString().substr(0, 3) + '****' + phone.toString().substr(7)
				}
			},
			fixPrice() {
				return (price) => {
					return price.toFixed(2).toString().split(".")[0] +"."+ price.toFixed(2).toString().split(".")[1]
				}
			},
			firstPrice(){
				return (price) => {
					return price.toFixed(2).toString().split(".")[0]
				}
			},
			lastPrice(){
				return (price) => {
					return price.toFixed(2).toString().split(".")[1]
				}
			}

		},

		onShow() {
			this.initialData()
		},

		methods: {
			initialData() {
				// 初始化默认地址
				let address = uni.getStorageSync('defaultAddress')
				if (Object.keys(address).length == 0) {
					this.hasAddress = false
				} else {
					this.hasAddress = true
					this.address = address
				}

				// 获取商品信息
				this.orderList = uni.getStorageSync('submitOrder')

				// 计算商品数据
				let totalPrice = 0
				for (let item of this.orderList) {
					totalPrice += item.num * item.type.fee
				}
				this.totalPrice = totalPrice

			},

			paySelecter(way){
				this.paySelect = way
			},
			orderRemark(e){
				this.remark = e.detail.value
			},
			chooseAddr() {
				uni.navigateTo({
					url: "../address/address?chooseAddress=" + true
				})
			},
			btnPay() {
				this.showPay = true
			},
			confirmPay() {
				let shoplist = new Array()
				for(let item of this.orderList){
					let obj = {}
					obj.tid = item.type.id
					obj.num = item.num
					obj.mid = item.mid
					shoplist.push(obj)
				}
				let opts = {
					url:app.apis().newOrder,
					token:app.globalData.token,
					method:'POST',
					data:{
						address:this.address.id,
						orderList:JSON.stringify(shoplist)
					}
				}
				console.log(opts)
				app.httpRequest(opts).then(res=>{
					console.log('backdata order:',JSON.parse(res.data))
					let back = JSON.parse(res.data)
					let data = back.data
					if(back.code==200){
						// #ifdef MP-WEIXIN
						wx.requestPayment(
						{
							nonceStr:data.nonceStr,
							package:data.package,
							signType:'MD5',
							paySign:data.paySign,
							timeStamp:data.timeStamp,
							success:function(res){
								console.log(res)
							}
						}
						)
						// #endif
					}else{
						uni.showToast({
							icon:'loading',
							title:'网络繁忙'
						})
					}
					
					
					app.listOrder()
				})
				
				// console.log('提交订单前端触发成功')
				// uni.redirectTo({
				// 	url: '../../payment/pay/pay'
				// })
			},
			// wxPay(paydata){
			// 	wx.
			// }
			popupClose() {
				this.show = false
			},
			closePay() {
				this.showPay = false
			}
		}
	}
</script>

